"use client";

import { useParams } from "next/navigation";
import Link from "next/link";
import path from "path";
import fs from "fs";

export default function WeekPage() {
  const params = useParams(); // now works
  const { id: division, week } = params;

  // Path to files
  const replayDir = path.join(process.cwd(), `public/d${division}/w${week}`);
  let files: string[] = [];

  if (fs.existsSync(replayDir)) {
    files = fs.readdirSync(replayDir).filter(f => f.endsWith(".html"));
  }

  return (
    <div className="min-h-screen flex flex-col items-center justify-start bg-gray-900 p-4 sm:p-6">
      <h1 className="text-3xl sm:text-4xl font-bold text-white mb-6 text-center">
        Division {division} - Week {week} Replays
      </h1>

      {files.length === 0 ? (
        <p className="text-gray-300">No replays uploaded yet.</p>
      ) : (
        <div className="w-full max-w-md flex flex-col gap-3">
          {files.map(file => (
            <Link
              key={file}
              href={`/d${division}/w${week}/${file}`}
              className="block bg-indigo-600 hover:bg-indigo-700 text-white font-semibold p-3 rounded-lg shadow-md text-center transition-transform transform hover:-translate-y-0.5"
            >
              {file.replace(".html", "")}
            </Link>
          ))}
        </div>
      )}
    </div>
  );
}